import React, { useContext, useEffect } from 'react'
import { OptionsContext } from './OptionsContext';
import Chart from "react-google-charts";


export default function OptionSummary() {

    const {
        listOfOptions,
        winTrades,
        setWinTrades,
        loseTrades,
        setLoseTrades,
        winPercents,
        setWinPercents,
        losePercents,
        setLosePercents
    } = useContext(OptionsContext);


    // get total profit by net lost and wins when list of options is updated
    useEffect(() => {
        setWinTrades(listOfOptions.filter(options => {
            return options.totalProfit > 0
        }))
        setLoseTrades(listOfOptions.filter(options => {
            return options.totalProfit < 0
        }))
    }, [listOfOptions])

    //get percentages and add to chart
    useEffect(() => {
        setWinPercents((winTrades.length % listOfOptions.length))
        setLosePercents((loseTrades.length % listOfOptions.length))
    })


    return (
        <div className="optionsummary">
            <Chart
                width={'500px'}
                height={'300px'}
                chartType="PieChart"
                loader={<div>Loading Chart</div>}
                data={[
                    ['Task', 'Hours per Day'],
                    ['Winning Trades', winPercents],
                    ['Losing Trades', losePercents],
                ]}
                options={{

                    legend: {
                        textStyle: {
                            color: 'white',
                            fontSize: '12'
                        }
                    },
                    titleTextStyle: {
                        color: 'white',
                        fontSize: '20',
                        fontName: 'Fugaz One'
                    },
                    title: 'Trade Summary',
                    size: '2rem',
                    backgroundColor: 'transparent',
                    colors: ['rgb(52, 38, 175)', 'red'],
                    fontFamily: 'Fugaz One',
                    is3D: true,
                }}
                rootProps={{ 'data-testid': '1' }}
            />

            <div className="trades">
                <p className="numtrades">
                    You have made <span style={{ fontFamily: 'Fugaz One', color: 'rgb(52, 38, 175)' }}>{listOfOptions.length}</span> trades
            </p>

                <p className="numtrades">
                    You have made <span style={{ color: 'green', fontFamily: 'Fugaz One' }}> {winTrades.length}</span> profitable trade(s)
                </p>

                <p className="numtrades">
                    You have made <span style={{ color: 'red', fontFamily: 'Fugaz One' }}> {loseTrades.length}</span> losing trade(s)
                </p>

            </div>
        </div >
    )
}
