import React, { useContext, useEffect } from 'react';
import { OptionsContext } from './OptionsContext';




const Navbar = () => {

    const {
        listOfOptions,
        totalReturn,
        setTotalReturn,
    } = useContext(OptionsContext);

    // get the total list of profits and add to overall balance
    const getTotal = () => {
        const newProfitArr = []
        for (let i = 0; i < listOfOptions.length; i++) {
            newProfitArr.push(listOfOptions[i].totalProfit)
        }

        return newProfitArr.reduce((accumulator, currentValue) => {
            if (isNaN(accumulator) || isNaN(currentValue)) {
                return accumulator;
            }
            else return accumulator + currentValue
        }, 0);
    }
    useEffect(() => {
        setTotalReturn(getTotal())

    })

    // const gainOptionsTab = (option) => {
    //     let clonedTable = [...listOfOptions]
    //     const filteredItem = clonedTable.filter(op => {
    //         return op.totalProfit > 0;
    //     }).map(option => {
    //         return option
    //     })
    //     return setListOfOptions(filteredItem)
    // }


    //toggle in out menu when you click on trade summary
    const getTradeSummary = () => {
        const tradeSummary = document.querySelector('.optionsummary');
        tradeSummary.classList.toggle('transIn')

    }

    return (

        <nav className="navbar">
            <h1 id="gains">stock<span>ER</span></h1>
            <h2 style={{ color: totalReturn >= 0 ? 'green' : 'red' }}>${totalReturn.toFixed(2)}</h2>
            <ul>
                <li onClick={getTradeSummary} className="tradingsummary">Trade Summary</li>
            </ul>
        </nav >

    )
}


export default Navbar;