import React, { useEffect, useContext, useState } from 'react';
import { OptionsContext } from './OptionsContext';
import { v1 as uuidv1 } from 'uuid';



export default function Inputs(lists) {

    const [sortDirection, setSortDirection] = useState(true)
    const [sortDirections, setSortDirections] = useState(true)

    const clock = new Date().toLocaleDateString();

    const {
        name,
        setName,
        price,
        setPrice,
        amountOfOptions,
        id,
        setId,
        setAmountOfOptions,
        totalAmountSpent,
        setTotalAmountSpent,
        listOfOptions,
        setListOfOptions,
        amountOfOptionsSold,
        setAmountOfOptionsSold,
        optionPriceSoldAt,
        setOptionPriceSoldAt,
        totalProfit,
        setTotalProfit,
        darkMode,
        setDarkMode
    } = useContext(OptionsContext);

    const handleSubmit = (e) => {
        e.preventDefault()
        e.target.reset();
        addListOfOptions(
            {
                clock,
                name,
                price,
                amountOfOptions,
                totalAmountSpent,
                amountOfOptionsSold,
                optionPriceSoldAt,
                totalProfit,
                id

            }
        )
    }
    // return 0 when the amount of options sold is over the initial amount of options purchased
    const alertOverOptions = (e) => {
        if (amountOfOptions < amountOfOptionsSold) {
            return setAmountOfOptionsSold(0)
        }
    }

    alertOverOptions()


    //get value of inputs
    const getInputValue = (hookSetter) => (e) => {
        let { value } = e.target;
        return hookSetter(value)
    }

    // set listofoptions to filter
    const addListOfOptions = (lists) => {
        setListOfOptions([...listOfOptions, lists])
        setId(uuidv1())
    }

    //add list of options to local storage
    useEffect(() => {
        let getOptions = localStorage.getItem('options');
        if (getOptions) {
            setListOfOptions(JSON.parse(getOptions))
        } else return null
    }, []);

    //retrieve items from storage
    useEffect(() => {
        localStorage.setItem('options', JSON.stringify(listOfOptions))
    }, [listOfOptions])

    // store dark theme to local storage
    useEffect(() => {
        localStorage.setItem('dark', JSON.stringify(darkMode))
    }, [darkMode]);

    // get values from inputs and calculate 
    useEffect(() => {
        setTotalAmountSpent((price * amountOfOptions) * 100)
        setTotalProfit((optionPriceSoldAt - price) * (amountOfOptionsSold * 100))
    })

    //delete items when you click x
    const deleteOption = (id) => {
        const deleteItem = listOfOptions.filter(option => {
            return option.id !== id;
        })
        setListOfOptions(deleteItem)
    }
    // sort table when you click on the arrows 
    const sortTable = (key) => {
        setSortDirection(!sortDirection);
        const clonedOptions = [...listOfOptions];
        clonedOptions.sort((a, b) => {
            return sortDirection ? a[key] - b[key] : b[key] - a[key];
        })
        setListOfOptions(clonedOptions);

        //clickable arrows to sort 
        const arrows = document.querySelectorAll('.fas')
        arrows.forEach(arrow => {
            if (sortDirection === true) {
                arrow.classList.add('arrowup')
            } else if (sortDirection === false) {
                arrow.classList.remove('arrowup')
            }
        })
    }

    const sortByName = (key) => {
        setSortDirection(!sortDirection);
        const clonedOptions = [...listOfOptions];
        clonedOptions.sort((a, b) => {

            if (sortDirection) {
                return (a[key] > b[key]) ? 1 : -1;
            } else {
                return (a[key] < b[key]) ? 1 : -1;
            }
        });
        setListOfOptions(clonedOptions);

        const arrows = document.querySelectorAll('.fas')
        arrows.forEach(arrow => {
            if (sortDirection === true) {
                arrow.classList.add('arrowup')
            } else if (sortDirection === false) {
                arrow.classList.remove('arrowup')
            }
        })
    }

    return (
        <div className={darkMode ? 'dark-mode' : null}>
            <form onSubmit={handleSubmit}>
                <input type="text" className="input stockname" placeholder="Enter Stock Symbol" onChange={getInputValue(setName)} />
                <input type="text" className="input stockprice" placeholder="Enter Option Price" onChange={getInputValue(setPrice)} />
                <input type="text" className="input stockamount" placeholder="Enter Number Of Option(s)" onChange={getInputValue(setAmountOfOptions)} />
                <input type="text" className="input stockpricesold" placeholder="Enter Option Price Sold" onChange={getInputValue(setOptionPriceSoldAt)} />
                <input type="text" className="input stockamountsold" placeholder="Enter Number Of Option(s) Sold" onChange={getInputValue(setAmountOfOptionsSold)} />
                <button type="submit" className="btn">Submit</button>
            </form>
            <div className="toggle-darkMode togglecontainer">
                <span className="toggle">
                    <input type="checkbox"
                        checked={darkMode}
                        onChange={() => setDarkMode(prevMode => !prevMode)}
                        id="checkbox"
                        className="checkbox"
                    />
                    <label htmlFor="checkbox" />
                </span>
                <i className="fas fa-adjust"></i>
            </div>
            <div className="outputs" >
                <table>
                    <thead>
                        <tr>
                            <th></th>
                            <th onClick={() => sortTable('clock')}>Date <span><i onClick={() => sortTable('clock')} className="fas fa-sort-up"></i></span></th>
                            <th onClick={() => sortByName('name')}>Stock Name <span><i onClick={() => sortTable('name')} className="fas fa-sort-up"></i></span></th>
                            <th onClick={() => sortTable('price')}>Price Of Option <span><i onClick={() => sortTable('price')} className="fas fa-sort-up"></i></span></th>
                            <th onClick={() => sortTable('amountOfOptions')}>Number Of Options <span><i onClick={() => sortTable('amountOfOptions')} className="fas fa-sort-up"></i></span></th>
                            <th onClick={() => sortTable('totalAmountSpent')}>Total Amount Spent <span><i onClick={() => sortTable('totalAmountSpent')} className="fas fa-sort-up"></i></span></th>
                            <th onClick={() => sortTable('optionPriceSoldAt')}>Option Sold At <span><i onClick={() => sortTable('optionPriceSoldAt')} className="fas fa-sort-up"></i></span></th>
                            <th onClick={() => sortTable('amountOfOptionsSold')}>Options Sold <span><i onClick={() => sortTable('amountOfOptionsSold')} className="fas fa-sort-up"></i></span></th>
                            <th onClick={() => sortTable('totalProfit')}>Proft <span><i onClick={() => sortTable('totalProfit')} className="fas fa-sort-up"></i></span></th>
                        </tr>
                    </thead>
                    {listOfOptions.map((option) => {
                        return (
                            <tbody key={uuidv1()}>
                                <tr>
                                    <td title="delete" onClick={() => deleteOption(option.id)}><span className="delete">x</span></td>
                                    <td>{option.clock}</td>
                                    <td>{option.name.toUpperCase()}</td>
                                    <td>${option.price}</td>
                                    <td>{option.amountOfOptions}</td>
                                    <td>${option.totalAmountSpent.toFixed(2)}</td>
                                    <td>${option.optionPriceSoldAt}</td>
                                    <td>{option.amountOfOptionsSold}</td>
                                    <td style={{ color: option.totalProfit >= 0 ? 'green' : 'red' }}>${option.totalProfit.toFixed(2)}</td>
                                </tr>
                            </tbody>
                        )
                    })}
                </table>
            </div>
        </div>
    )
}
