import React, { useState, createContext } from 'react';
import { v1 as uuidv1 } from 'uuid';




export const OptionsContext = createContext();


export const OptionsProvider = props => {
    const getInitialMode = () => {
        const savedMode = JSON.parse(localStorage.getItem('dark'));
        return savedMode || false
    }

    const [name, setName] = useState('');
    const [price, setPrice] = useState(0);
    const [amountOfOptions, setAmountOfOptions] = useState(0);
    const [totalAmountSpent, setTotalAmountSpent] = useState(0);
    const [listOfOptions, setListOfOptions] = useState([]);
    const [id, setId] = useState(uuidv1());
    const [amountOfOptionsSold, setAmountOfOptionsSold] = useState(0);
    const [optionPriceSoldAt, setOptionPriceSoldAt] = useState(0);
    const [totalProfit, setTotalProfit] = useState(0);
    const [totalReturn, setTotalReturn] = useState(0);
    const [newTable, setNewTable] = useState(listOfOptions);
    const [winTrades, setWinTrades] = useState([]);
    const [loseTrades, setLoseTrades] = useState([]);
    const [winPercents, setWinPercents] = useState([]);
    const [losePercents, setLosePercents] = useState([]);
    const [darkMode, setDarkMode] = useState(getInitialMode());

    const value = {
        name,
        setName,
        price,
        setPrice,
        amountOfOptions,
        setAmountOfOptions,
        totalAmountSpent,
        setTotalAmountSpent,
        listOfOptions,
        setListOfOptions,
        id,
        setId,
        amountOfOptionsSold,
        setAmountOfOptionsSold,
        optionPriceSoldAt,
        setOptionPriceSoldAt,
        totalProfit,
        setTotalProfit,
        totalReturn,
        setTotalReturn,
        newTable,
        setNewTable,
        winTrades,
        setWinTrades,
        loseTrades,
        setLoseTrades,
        winPercents,
        setWinPercents,
        losePercents,
        setLosePercents,
        darkMode,
        setDarkMode,
    };

    return (
        <OptionsContext.Provider value={value}>
            { props.children}
        </OptionsContext.Provider>
    );
}